.game_bar {
  position: absolute;
  z-index: 1;
  user-select: none;

  & .game_bar_wrapper {
    padding: 10px 50px;

    @media (--desktopS) {
      padding: 0 40px;
    }
  }
}

.game_bar_bg {
  background-color: var(--main-bg-color);
  border: 1px solid var(--main-font-color);
}
